.photography-course__title {
  font-weight: 400;
  font-size: 47px;
  line-height: 56px;
  margin-top: 14px;
  margin-bottom: 60px;
}

@media screen and (min-width: 768px) {
  .photography-course__title {
    font-size: 100px;
    line-height: 110px;
    margin-top: 40px;
  }
}

@media screen and (min-width: 1440px) {
  .photography-course__title {
    width: 45vw;
    margin-top: 0;
  }
}